@font-face {
  font-weight: 700;
  font-style: normal;
  font-family: "Play";
  font-display: swap;
  src: url("./../fonts/play-bold.woff") format("woff"), url("./../fonts/play-bold.woff2") format("woff2"); }

@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Play";
  font-display: swap;
  src: url("./../fonts/play-regular.woff") format("woff"), url("./../fonts/play-regular.woff2") format("woff2"); }

@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Roboto";
  font-display: swap;
  src: url("./../fonts/roboto-regular.woff") format("woff"), url("./../fonts/roboto-regular.woff2") format("woff2"); }

@font-face {
  font-weight: 500;
  font-style: normal;
  font-family: "Roboto";
  font-display: swap;
  src: url("./../fonts/roboto-medium.woff") format("woff"), url("./../fonts/roboto-medium.woff2") format("woff2"); }

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  background-color: #000000; }

@media (min-width: 1440px) {
  .wrapper {
    width: 100%; }
  .container {
    margin: 0 auto;
    width: 1440px;
    padding-left: 120px;
    padding-right: 120px; } }

* {
  box-sizing: border-box; }

.about {
  background-color: #ffffff;
  padding-top: 120px;
  padding-bottom: 180px; }

.about__title {
  color: #000000;
  font-family: "Play", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 52px;
  margin: 0; }

.about__description {
  color: rgba(0, 0, 0, 0.5);
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 40px; }

.content__title {
  font-family: "Play", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 52px;
  margin: 0;
  padding-bottom: 20px; }

.content__description {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.5);
  margin: 0;
  padding-bottom: 80px; }

.content {
  padding-top: 120px;
  padding-bottom: 80px;
  border-top-left-radius: 80px;
  border-top-right-radius: 80px;
  z-index: 30;
  margin-top: -90px;
  background-color: #000000; }

.content-item.content-item--text {
  border-radius: 24px;
  position: relative;
  overflow: hidden;
  width: 784px;
  height: 400px; }

.content-item.content-item--text::after {
  content: "";
  background: linear-gradient(116.95deg, #e64445 0%, #7356bf 100%);
  position: absolute;
  width: 100%;
  height: 100%; }

.content-item--text .content-item__body {
  background-color: #000000;
  position: absolute;
  z-index: 2;
  width: 779px;
  height: 396px;
  top: 2px;
  left: 2px;
  border-radius: 24px; }

.content__data {
  display: grid;
  row-gap: 48px;
  column-gap: 48px;
  grid-template-columns: max-content max-content;
  grid-template-rows: max-content max-content; }

.content-item.content-item--title {
  border: 2px solid #ffffff;
  width: 368px;
  height: 400px;
  border-radius: 24px; }

.main-header .container {
  display: flex;
  flex-direction: column;
  padding-top: 40px;
  height: 100vh;
  position: relative; }

.main-header__title {
  font-family: "Play", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 56px;
  line-height: 60px;
  margin: 0;
  margin-top: auto;
  padding-bottom: 75px; }

.link {
  display: flex;
  padding-top: 32px; }

.link__data {
  text-decoration: none;
  color: #00d0ff;
  font-size: 20px;
  line-height: 24px;
  margin-left: auto;
  cursor: pointer; }

.main-footer {
  background-color: #28282c;
  padding-top: 120px;
  padding-bottom: 120px; }

.main-footer__text {
  font-family: "Play", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 52px; }

.main-navigation__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  padding-bottom: 80px; }

.main-navigation__item {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  padding-right: 56px;
  position: relative; }
  .main-navigation__item:hover {
    color: #00d0ff; }
  .main-navigation__item:last-child {
    color: rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    padding-right: 0; }

.main-navigation__market-link {
  margin-left: 24px;
  margin-top: 4px; }

.main-navigation__item:nth-child(1)::after,
.main-navigation__item:nth-child(2)::after {
  content: "/";
  position: absolute;
  top: 0;
  right: 24px;
  color: rgba(255, 255, 255, 0.12);
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px; }

.main {
  border-top-left-radius: 80px;
  border-top-right-radius: 80px;
  z-index: 20;
  overflow: hidden; }

.video-menu {
  display: flex;
  height: 400px; }

.video-menu__item {
  width: 163px;
  cursor: pointer;
  position: relative;
  transition: all 0.3s; }
  .video-menu__item--current {
    flex-grow: 1;
    width: 712px; }

.video-menu__media {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover; }

.video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1; }
